.home-footer {
    width: 100%;
    height: 100%;
    padding: 0px 10px;
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    & > div {
        display: flex;
        &.wrapper-left{
            display: flex;
            align-items: center;
            .room-name{
                // white-space: nowrap;
                overflow: hidden;
            }
            button{
                width: 40px;
                height: 40px;
                display: flex;
                flex-direction: column;
                align-items: center;
                i{
                    font-size: 18px;
                }
                background-color: #333;
                &:hover{
                    color: royalblue;
                }
            }
        }
        button {
            width: 46px;
            height: 46px;
            background-color: transparent;
            border: none;
            outline: none;
            cursor: pointer;
            margin: 0 4px;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 22px;
            border-radius: 50%;
            &.circle {
                width: 40px;
                height: 40px;
                font-size: 18px;
                background-color: #3c4043;
            }
            &.end-call {
                height: 40px;
                background-color: #3c4043;
                border-radius: 20px;
                font-size: 20px;
                background: #ea4335;
                width: 50px;
                i {
                    position: relative;
                    top: 1px;
                    transform: rotate(135deg);
                }
            }
            &:hover {
                background-color: #3c404359;
            }
        }
    }
}

.menu-footer{
    .ant-dropdown-menu-item{
        height: 36px;
        &:hover{
            background-color: royalblue;
            color: #fff;
        }
    }
    .ant-dropdown-menu-item-icon{
        width: 25px;
        font-size: 16px;
        font-weight: bold;
    }
}