.ant-modal.modal-setting-camera{
    .ant-modal-content{
        width: 100%;
        .wrapper-input{
            button{
                width: 36px;
                height: 30px;
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: royalblue;
                border: none;
                outline: none;
                color: #fff;
                border-radius: 3px;
                cursor: pointer;
            }
        }
    }
}