.layout-sub-smallview{
    background-color: #202124;
    .header-smallview{
        background-color: transparent;
        height: 140px;
        padding: 0 20px;
        border-bottom: 1px solid #3c3c3c;
        // padding: 10px;
        overflow-y: auto;
        &::-webkit-scrollbar {
            width: 6px;
        }

        /* Track */
        &::-webkit-scrollbar-track {
            background: #f1f1f1;
        }

        /* Handle */
        &::-webkit-scrollbar-thumb {
            background: #888;
        }

        /* Handle on hover */
        &::-webkit-scrollbar-thumb:hover {
            background: #555;
        }
        .ant-row.row-list-user {
            padding: 2px;
            .col-user-component {
                padding: 2px;
                .wrapper {
                    width: 100%;
                    height: 100%;
                    min-height: 98px;
                    background: #333;
                    border-radius: 2px;
                    .user-name {
                        color: #fff;
                        font-size: 12px;
                        padding: 0 5px;
                        display: flex;
                        line-height: 22px;
                        justify-content: space-between;
                        button.btn-audio{
                            background-color: transparent;
                            border: none;
                            outline: none;
                            color: orange;
                            width: 22px;
                            height: 22px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            cursor: pointer;
                            &.hidden{
                                display: none;
                            }
                        }
                    }
                    .row-wrapper-video {
                        width: 100%;
                        padding: 3px;
                        padding-top: 0;
                        .col-wrapper-video {
                            padding: 2px;
                            .wrapper-video {
                                width: 100%;
                                height: 100%;
                                &.video-empty {
                                    height: 98px;
                                    display: flex;
                                    justify-content: center;
                                    align-items: center;
                                    font-size: 12px;
                                    border: 1px solid #444;
                                    color: #fff;
                                }
                                .producer-to-consumer {
                                    padding: 2px;
                                    border: 1px solid #444;
                                    min-height: 98px;
                                    &.active {
                                        border: 1px solid orange;
                                    }
                                }

                                video {
                                    max-height: 98px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}