#home-page {
    width: 100%;
    height: 100vh;
    background: #202124;
    box-sizing: border-box;
    overflow: hidden;
    .layout-sub {
        background-color: transparent;
        padding: 10px;
        #home-page-content{
            margin: 0 10px;
        }
    }
}

#home-page-footer {
    height: 60px;
    background: transparent;
    padding: 0;
    margin: 0;
    font-size: 1rem;
}

#home-page-chat-sider {
    background-color: #fff;
    border-radius: 4px;
}
