.login-page{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    .login-wrapper{
        padding: 20px;
        box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
        max-width: 360px;
        width: 96%;
        .label{
            color: #333;
            margin-top: 20px;
        }
    }
}