.home-sider-chat {
    width: 100%;
    height: 100%;
    #chat-panel{
        height: 100%;
        .chat-history{
            /* width */
            &::-webkit-scrollbar {
                width: 6px !important;
            }
    
            /* Track */
            &::-webkit-scrollbar-track {
                background: #f1f1f1;
            }
    
            /* Handle */
            &::-webkit-scrollbar-thumb {
                background: #888;
            }
    
            /* Handle on hover */
            &::-webkit-scrollbar-thumb:hover {
                background: #555;
            }
            h5{
                margin-left: 5px !important;
                color: royalblue !important;
                font-size: 12px !important;
            }
        }
    }
}
